import { createRouter, createWebHistory } from 'vue-router';
import UserLogin from '../views/UserLogin.vue';
import UserRegister from '../views/UserRegister.vue';
import GoodsList from '../views/GoodsList.vue';
import AdminDashboard from '../views/AdminDashboard.vue';
import AuctionDetail from '../views/AuctionDetail.vue';
import AuctionResult from '../views/AuctionResult.vue';
import UserM from '../views/UserM.vue'; // 导入 UserM.vue 组件

const routes = [
  { path: '/', redirect: '/login' },
  { path: '/login', component: UserLogin },
  { path: '/register', component: UserRegister },
  { path: '/goods-list', component: GoodsList },
  { path: '/admin-dashboard', component: AdminDashboard },
  { path: '/auction-detail/:id', component: AuctionDetail, props: true },
  { path: '/auction-result/:id', component: AuctionResult, props: true },
  { path: '/user-profile', component: UserM, meta: { requiresAuth: true } } // 新增个人信息路由，并添加元数据 requiresAuth
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});


// 全局前置守卫，用于检查用户是否登录
router.beforeEach((to, from, next) => {
  const isLoggedIn = localStorage.getItem('isLoggedIn') === 'true'; // 从本地存储中检查登录状态，可根据实际情况修改
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!isLoggedIn) {
      next({ path: '/login' }); // 如果需要登录且用户未登录，重定向到登录页面
    } else {
      next();
    }
  } else {
    next();
  }
});


export default router;